












//需要在当前页面手动设置图片的高度  .lidada-swiper swiper{}
export default {
	name: "lidada-swiper",
	data() {
		return {
			indicatorDots: true,	//指示点
		};
	},
	props: {
		listData : Object,	//传入的数据对象
		jump : String	,//是否跳转链接
		autoplay: String,	//是否自动播放
		statusBarHeight: String,	//是否预留手机状态栏高度
	},
	methods: {
		onClick(e) {	//点击图片触发
			let href = e.currentTarget.dataset.href;
			if(href && this.jump){
				uni.navigateTo({
					url: href
				});
			}
		}
	}
}
