























	
	//引入lidada-swiper组件
	import lidadaSwiper from '../../../components/lidada_swiper.vue'
	//引入uniGrid九宫格组件
	import uniGrid from '@dcloudio/uni-ui/lib/uni-grid/uni-grid.vue'
	
	import {
		mapState
	} from 'vuex';
	
	export default {
		components: {	//注册或获取全局组件
			lidadaSwiper,
			uniGrid
		},
		data() {
			return {
				"banner": [],	//banner数据容器
				"grid": [],	//宫格导航数据容器
			};
		},
		onLoad() {	//监听页面加载
			//更新vuex存储
			this.$store.commit('userInfoUpdate');
			//开启下拉监听功能
			uni.startPullDownRefresh();
		},
		computed: {
			...mapState(['localhost','userInformation'])
		},
		onShow() {
			//更新vuex存储
			this.$store.commit('userInfoUpdate');
			//显示用户信息
			console.info(JSON.stringify(this.userInformation))
		},
		methods: {	//事件处理器
			bindClick(e) {
				let href = this.grid[e.index].href;
				if(href){
					uni.navigateTo({
						url: href
					});
				}else{
					uni.showToast({ title: '功能开发中...', icon: "none" });
				}
			},
			cardClick(e) {
				console.log(e);
				console.log(e.currentTarget.dataset.href);
// 				let href = this.grid[e.index].href;
// 				if(href){
// 					uni.navigateTo({
// 						url: href
// 					});
// 				}else{
					uni.showToast({ title: '功能开发中...', icon: "none" });
				// }
			}
		},
		onPullDownRefresh() {	//下拉更新功能模块
			//页面加载提示信息
			uni.showLoading({
				title: '加载中请稍后...'
			});
			//加载banner
			uni.request({
				url: this.localhost + '/indexBanner',
				method: 'POST',
				success: res => {
					var data = res.data.data;
					let dataLength = data.length;
					//存储状态
					this.indicatorDots = dataLength > 1 ? true : false;
					this.banner = data;
					console.log('请求完成')
				},
				fail: () => {},
				complete: () => {
					setTimeout(function () {
						uni.hideLoading();	//隐藏加载提示
						uni.stopPullDownRefresh();	//隐藏下拉提示
					}, 1000);
				}
			});
			
			//加载宫格部分
			uni.request({
				url: this.localhost + '/indexGrid',
				method: 'POST',
				success: res => {
					var data = res.data.data;
					this.grid = data;
				},
				fail: () => {},
				complete: () => {
					setTimeout(function () {
						uni.hideLoading();	//隐藏加载提示
						uni.stopPullDownRefresh();	//隐藏下拉提示
					}, 1000);
				}
			});
		}
	}
